<template>
  <div class="dots">
    <canvas ref="canvas"></canvas>
  </div>
</template>

<script>
import DotsRun from 'common/js/dotsRun.js'
export default {
  name: 'dots',
  methods: {
    initCanvasDotsRun(num = 200) {
      this.dots = null
      this.dots = new DotsRun(this.$refs.canvas, {
        dotsNumber: num,
        ArcColor: '#999',
        canvasWidth: 'full',
        canvasHeight: 'full',
        mouseColor: '#f60',
        speedX: 2,
        speedY: 3,
        isRandomDots: true,
        lineLength: 200,
        mouseLineLength: 300,
        // isClick: true,
        isHover: true
      });
    },
    click(ev) {
      this.dots.addDots(ev)
    },
    mouseMove(ev) {
      this.dots.mouseMove(ev)
    },
    mouseLeave() {
      this.dots.mouseLeave()
    },
    resize() {
      this.dots.clear();
      this.Utils.isPC() ? this.initCanvasDotsRun() : this.initCanvasDotsRun(30);
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.Utils.isPC() ? this.initCanvasDotsRun() : this.initCanvasDotsRun(30);
      window.addEventListener('resize', this.resize, true)
      this.$isBlur((res) => {
        res ? this.dots.initCanvas() : this.dots.clear();
      })
    })
  },
  destroyed() {
    window.removeEventListener('resize', this.resize, true)
  }
}
</script>

<style lang="less" scoped>
.dots {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  width: 100%;
  height: 100%;
  > canvas {
    width: 100%;
    height: 100%;
  }
}
</style>